export default [
  { path: '/mx',
    // redirect: '/question/question',
    name: 'mx',
    component: resolve => require(['@/view/mx/index'], resolve),
    children: [
      {path: 'device_id', name: 'message2', component: resolve => require(['@/view/mx/message2'], resolve)},
      {path: 'RFID', name: 'RFID', component: resolve => require(['@/view/mx/RFID'], resolve)},
      {path: 'TimeCloud', name: 'TimeCloud', component: resolve => require(['@/view/mx/TimeCloud'], resolve)},
      {path: 'TimeCloud1', name: 'TimeCloud1', component: resolve => require(['@/view/mx/TimeCloud1'], resolve)},
      {path: 'listenSocket', name: 'listenSocket', component: resolve => require(['@/view/mx/listenSocket'], resolve)}
    ]
  }
]



